:root {
  --font-main: "Poppins", sans-serif;
  --color-bg-primary: #1c1b21;
  --color-bg-faded: rgba(28, 27, 33, 0.8);
  --color-bg-slide: #2f2b2b;
  --color-accent-primary: #7a66cc;
  --color-accent-secondary: rgba(122, 102, 204, 0.3);
  --color-accent-faded: rgba(122, 102, 204, 0.7);
  --color-white: #ffffff;
  --color-white-delimiter: rgba(255, 255, 255, 0.5);
  --color-white-faded: rgba(255, 255, 255, 0.1);
  --color-font-primary: #ffffff;
  --color-font-faded: rgba(255, 255, 255, 0.7);
  --color-icon-bg: #2c2c2c;
}

/*
---- Require libs/_vars.scss
*/
@font-face {
  font-family: "iconfont";
  src: url("../fonts/iconfont.eot?rtseok");
  src: url("../fonts/iconfont.eot?rtseok#iefix") format("embedded-opentype"), url("../fonts/iconfont.ttf?rtseok") format("truetype"), url("../fonts/iconfont.woff?rtseok") format("woff"), url("../fonts/iconfont.svg?rtseok#iconfont") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "iconfont" !important;
  speak-as: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

