/*
---- Main settings
*/
body {
  background-color: var(--color-bg-primary);
  font-family: var(--font-main);
  color: var(--color-font-primary);
}
body.is-lock {
  overflow: hidden;
}

[class*=__container] {
  max-width: 71.25rem;
  margin: 0 auto;
  padding: 0 0.9375rem;
}

#app {
  display: grid;
  grid-template-rows: 1fr auto 1fr;
  height: 100%;
}

/*
---- Require includes/_header.scss
*/
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 20;
  transition: background 0.3s ease 0s;
}
.header.is-scroll {
  background: var(--color-accent-faded);
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 59.375rem;
  margin: 0 auto;
  padding: 1.875rem 0.9375rem;
  transition: padding 0.3s ease 0s;
}
.header__inner.is-scroll {
  padding: 0.625rem 0.9375rem;
}
.header__logo {
  margin: 0 4.0625rem 0 0;
}
@media (max-width: 35.99875rem) {
  .header__logo img {
    width: 11.25rem;
  }
}
@media (max-width: 61.99875rem) {
  .header__nav {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 20;
    width: 100%;
    height: 100%;
    padding: 2.5rem;
    background: var(--color-bg-primary);
    transform: translate(100%, 0);
    transition: transform 0.3s ease 0s;
    overflow: auto;
  }
  .header__nav.is-open {
    transform: translate(0, 0);
  }
}
.header__list {
  display: flex;
  align-items: center;
}
@media (max-width: 61.99875rem) {
  .header__list {
    flex-direction: column;
    align-items: flex-start;
  }
}
.header__item:not(:last-child) {
  margin: 0 3.125rem 0 0;
}
.header__link {
  position: relative;
  display: inline-block;
  padding: 0 0 0.3125rem 0;
  font-size: 1.125rem;
  font-weight: 600;
  color: inherit;
  transition: color 0.3s ease 0s;
}
@media (max-width: 61.99875rem) {
  .header__link {
    display: inline-block;
    padding: 0.625rem;
  }
}
.header__link::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 0.125rem;
  background: var(--color-white);
  transition: width 0.3s ease 0s;
}
.header__link:hover::after {
  width: 100%;
}
.header__burger {
  display: none;
}
@media (max-width: 61.99875rem) {
  .header__burger {
    position: relative;
    z-index: 25;
    display: grid;
    gap: 0.3125rem 0;
    margin: 0 -0.625rem 0 0;
    padding: 0.625rem;
    background: transparent;
    border: 0;
  }
}
.header__burger.is-active span:nth-child(1) {
  transform: translate(0, 0.5rem) rotate(45deg);
}
.header__burger.is-active span:nth-child(2) {
  opacity: 0;
}
.header__burger.is-active span:nth-child(3) {
  transform: translate(0, -0.5rem) rotate(-45deg);
}
.header__burger span {
  display: block;
  width: 1.875rem;
  height: 0.1875rem;
  border-radius: 0.1875rem 0;
  background: var(--color-white);
  transition: all 0.3s ease 0s;
}

